<template>
    <div class="footer">
        <div class="footermusic" @click="controlBtn">
            <audio id="track" preload="auto">
                <source src="https://vgmsite.com/soundtracks/world-of-warcraft-9-shadowlands-2020/alspkemitc/12.%20Pride%20and%20Penance.mp3" type="audio/mp3">
            </audio>
            <div id="player-container">
                <div id="play-pause" class="play">Play</div>
            </div>
        </div>
        <div class="footerlang">
            <div class="lang_buttons">
                <button class="button en_lang" @click="locale">
                    <span class="icon">
                        <ion-icon name="language"></ion-icon>
                    </span> {{ $t('locale') }}
                </button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Footer',
    methods: {
        locale() {
            if (this.$i18n.locale === 'ru') {
                this.$i18n.locale = 'en'
            } else {
                this.$i18n.locale = 'ru'
            }
        },
        controlBtn() {
            let track = document.getElementById('track')
            let playPause = document.getElementById('play-pause')

            if (track.paused) {
                track.play()
                playPause.className = 'pause'
            } else {
                track.pause()
                playPause.className = 'play'
            }
        }
    }
}
</script>

